/* AWARDS SECTION */

.awards {
    width: 100%;
    height: 158px;
    background-color: $gold;
    position: relative; }
.awards__slider {
    width: 795px;
    margin: 0 auto;
    @include vcenter;
    z-index: 1; }
.awards__slide {
    @include image('leaves.png');
    @include cover;
    width: 160px;
    height: 70px;
    position: relative; }
.awards__text {
    width: 85px;
    font-family: $charter-bold, sans-serif;
    font-size: em(12);
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    z-index: 1;
    @include vcenter; }
.awards__paginator {
    width: 920px;
    @include vcenter; }
.awards__prev {
    @include s(arrow_left);
    float: left;
    cursor: pointer; }
.awards__next {
    @include s(arrow_right);
    float: right;
    cursor: pointer; }
