/* CENTURIES SECTION */

.centuries {
    width: 100%;
    height: 444px;
    @include image('centuries_bg.jpg');
    @include cover;
    @include md {
        height: 350px; }
    @include sm {
        height: 280px; }
    @include xs {
        height: 230px; }
    @include r(454) {
        height: 180px; }
    @include r(325) {
        height: 130px; } }
.centuries__container {
    width: 100%;
    height: 100%;
    max-width: 938px;
    margin: 0 auto;
    position: relative; }
.centuries__bottle {
    width: 500px;
    height: 444px;
    @include image('bottle.png');
    @include cover;
    position: absolute;
    left: -105px;
    z-index: 999;
    @include md {
        width: 405px;
        height: 350px; }
    @include sm {
        width: 330px;
        height: 280px; }
    @include xs {
        width: 270px;
        height: 230px; }
    @include r(454) {
        width: 220px;
        height: 180px; }
    @include r(325) {
        width: 170px;
        height: 130px; } }
.centuries__banner {
    width: 100%;
    padding: 18px 25px;
    background-color: #fff;
    @include vcenter;
    @include md {
        padding: 10px; }
    @include sm {
        padding: 7px; } }
.centuries__banner_inner {
    border-top: 3px solid $gold;
    border-bottom: 3px solid $gold;
    color: $gold;
    @include clr; }
.centuries__content {
    float: right;
    text-align: left; }
.centuries__figure {
    font-family: $charter-bold, sans-serif;
    font-size: em(200);
    line-height: 1.1;
    display: inline-block;
    @include md {
        font-size: em(170); }
    @include sm {
        font-size: em(140); }
    @include xs {
        font-size: em(110); }
    @include r(454) {
        font-size: em(80); }
    @include r(325) {
        font-size: em(70); } }
.centuries__text {
    max-width: 265px;
    margin-left: 25px;
    display: inline-block;
    @include sm {
        max-width: 200px; }
    @include xs {
        max-width: 150px; }
    @include r(454) {
        max-width: 100px; }
    @include r(325) {
        max-width: 70px; } }
.centuries__text_large {
    font-family: $charter-bold, sans-serif;
    font-size: em(51);
    line-height: 0.9;
    @include md {
        font-size: em(44); }
    @include sm {
        font-size: em(37); }
    @include xs {
        font-size: em(30); }
    @include r(454) {
        font-size: em(23); }
    @include r(325) {
        font-size: em(18); } }
.centuries__text_small {
    margin-top: 5px;
    font-size: em(26);
    line-height: 1;
    @include md {
        font-size: em(20); }
    @include sm {
        font-size: em(14); }
    @include xs {
        font-size: em(12); }
    @include r(454) {
        font-size: em(11); } }
