/* HISTORY SECTION */

.history {
    width: 100%;
    min-height: 665px;
    position: relative;
    overflow: hidden;
    @include image('history_bg.jpg');
    @include cover; }
.history__container {
    width: percentage(1066/1920);
    min-width: 244px;
    background-color: #fff;
    padding: 20px;
    text-align: center;
    @include vcenter; }
.history__banner {
    border: 1px solid $gold;
    @include md {
        border: none; } }
.history__text {
    max-width: 517px;
    margin: 0 auto;
    padding: 90px 0; }
.history__year {
    width: 100%;
    border-top: 2px solid $gold;
    border-bottom: 2px solid $gold;
    color: $gold;
    position: relative; }
.history__text_year {
    width: 100%;
    font-size: em(20);
    line-height: 20px;
    position: absolute;
    span {
        background-color: #fff;
        padding: 0 10px;
        display: inline-block; } }
.history__text_year_top {
    top: -20px; }
.history__text_year_bottom {
    bottom: -10px; }
.history__figure {
    font-family: $charter-bold, sans-serif;
    font-size: em(136);
    font-weight: 600;
    @include md {
        font-size: em(126); }
    @include sm {
        font-size: em(116); }
    @include xs {
        font-size: em(106); }
    @include r(489) {
        font-size: em(86); } }
.history__text_small {
    font-size: em(20);
    color: $gold;
    position: relative;
    bottom: -15px; }
.history__text_large {
    font-family: $charter-regular, sans-serif;
    position: relative;
    bottom: -40px; }
