/* COMMENTS SECTION */

.comments {
    width: 100%;
    height: 370px;
    border: 23px solid $gold; }
.comments__slider {
    width: 100%;
    max-width: 815px;
    height: 100%;
    margin: 0 auto; }
.comments__slide {
    position: relative;
    min-height: 270px; }
.comments__slide_inner {
    width: 100%;
    @include vcenter; }
.comments__text {
    font-family: $charter-regular, sans-serif;
    text-align: center; }
.comments__meta {
    margin: 0 auto;
    display: table; }
.comments__author {
    text-align: right;
    display: table-cell;
    vertical-align: middle;
    @include xs {
        display: block; } }
.comments__name {
    font-size: em(20);
    font-weight: 500;
    color: $gold;
    @include sm {
        font-size: em(16); } }
.comments__position {
    @include sm {
        font-size: em(14); } }
.comments__signature {
    width: 170px;
    height: 70px;
    @include cover;
    display: table-cell; }
.comments__signature_first {
    @include image('signature_1.jpg'); }
.comments__signature_second {
    @include image('signature_1.jpg'); }
.comments__signature_third {
    @include image('signature_1.jpg'); }


